<template>
  <div class="videos">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="title-box">
        当前考点：<span>{{$route.query.name}}</span>
      </div>
      <div class="item">
        <p class="title">图片</p>
        <div v-for="(item,key) in form.unreelList" class="img-box">
          <img @click="bigImgUrl = item.fileUrl;imgDialog = true" :src="item.fileUrl" alt="">
          <div class="oper">
            <a :href="item.fileUrl" download target="_blank">下载</a>
            <i class="col"></i>
            <span @click="removePic(key)">删除</span>
          </div>
        </div>
        <no-data v-show="!form.unreelList.length" class="no-data">暂无数据</no-data>
        <div class="clear"></div>
      </div>

      <div class="item">
        <p class="title">视频</p>
        <div v-for="item in form.invigilateList" class="img-box">
          <img @click="showVideosDialog(item.fileUrl)" src="../../../../assets/img/资源4.png" alt="">
          <div class="oper">
            <a :href="item.fileUrl" download target="_blank">下载</a>
            <i class="col"></i>
            <span @click="removeVideo(key)">删除</span>
          </div>
        </div>
        <no-data v-show="!form.invigilateList.length" class="no-data">暂无数据</no-data>
        <div class="clear"></div>
      </div>
      <div class="btns mt30">
        <div @click="doSubmit()" class="btn-submit btn-loading mr15">提交</div>
        <div @click="$router.go(-1)" class="btn-clear">返回</div>
      </div>
      <uploader
          ref="uploadZone"
          :options="uploadOptions"
          :file-status-text="fileStatusText"
      >
        <uploader-unsupport></uploader-unsupport>
        <uploader-btn :attrs="attrs">
          <!--<img class="hand" src="../../../../assets/img/资源28@2x.png" alt="">-->
          <div class="btn-submit">上传文件</div>
        </uploader-btn>
        <div class="upload-box">
          <ul class="upload-ul">
            <li>文件</li>
            <li>大小</li>
            <li>速度</li>
            <li>操作</li>
          </ul>
          <uploader-list></uploader-list>
        </div>
      </uploader>

      <el-dialog width="1000px" class="dialog" :visible.sync="dialog">
        <div class="deplyer" id="dplayer"></div>
      </el-dialog>
      <el-dialog width="1000px" class="dialog" :visible.sync="imgDialog">
        <img :src="bigImgUrl" alt="">
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import 'dplayer/dist/DPlayer.min.css';
  import DPlayer from 'dplayer';
  export default {
    data() {
      return {
        nav: [
          {name: "考试管理", path: "/recruit/exam"},
          {name: "查看考点", path: "go(-1)"},
          {name: "监考视频", path: ""}
        ],
        attrs: {
          accept: 'video/*, image/*'
        },
        uploadOptions: {
          target: `${window.location.origin}${this.$conf.API_PATH}/uploadFileTrunk/trunkUpload.do`,
          testChunks: false,
          chunkSize: 2*1024*1024,
          singleFile: false,
          parseTimeRemaining: (timeRemaining, parsedTimeRemaining) => {
            return parsedTimeRemaining
              .replace(/\syears?/, '年')
              .replace(/\days?/, '天')
              .replace(/\shours?/, '小时')
              .replace(/\sminutes?/, '分钟')
              .replace(/\sseconds?/, '秒')
          }
        },
        // 修改分片上传文字
        fileStatusText: {
          success: '上传成功',
          error: '上传错误',
          uploading: '正在上传',
          paused: '已暂停',
          waiting: '等待中'
        },
        // 弹窗
        dialog: false,
        // 图片弹窗
        imgDialog: false,
        // 视频播放器实例
        dp: '',
        bigImgUrl: '',
        // 播放视频地址
        videoUrl: '',
        form: {
          examCentreId: this.$route.query.id,
          unreelList: [],
          invigilateList: [],
          envelopeList: []
        }
      }
    },
    methods: {
      // 获取播放视频
      async getVideo(id) {
        // this.dp = new DPlayer({
        //   preload: 'auto',
        //   loop: false,
        //   container: document.getElementById('dplayer'),
        //   video: {
        //     quality: quality,
        //     defaultQuality: quality.length - 1,
        //     pic: this.$conf.SOURCE_PATH + res.video.coverImg + '!636!416',
        //   }
        //
        // });
      },
      // 上传图片
      async uploadCover(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        if (file.size > 1024 * 1024 * 10) {
          this.$message.warning('图片不能超过10M')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/oss/upload.do',
          query: formData,
          el: '.cover-img',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.form.invigilateList = res
        this.$refs.uploadCover.value = null;
      },
      // 提交
      async doSubmit(){
        await this.$ajax({
          url: '/centre/addExamAttachment.do',
          query: this.form,
          el: '.btn-loading',
          noText: true
        })
        this.$remind('提交成功')
        this.$router.go(-1)
      },
      // 查询监考视频
      async getDetail(){
        let res = await this.$ajax({
          url: '/centre/getExamAttachment.do',
          query:{
            examCentreId: this.$route.query.id
          }
        })
        this.form.unreelList = res.unreelList
        this.form.invigilateList = res.invigilateList
        this.form.envelopeList = res.envelopeList
      },
      // 移除图片
      async removePic(key){
        await this.$confirm('您确认删除该图片吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        this.form.unreelList.splice(key,1)
      },
      // 移除视频
      async removeVideo(key){
        await this.$confirm('您确认删除该视频吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        this.form.invigilateList.splice(key,1)
      },
      // 展现视频弹窗
      showVideosDialog(url){
        this.videoUrl = url
        this.dialog = true
        setTimeout(()=>{
          this.dp = new DPlayer({
            preload: 'auto',
            loop: false,
            container: document.getElementById('dplayer'),
            video: {
              url: url
            }
          })
        })
      }
    },
    mounted(){
      this.getDetail()
      // 初始化编辑器
      // 分片上传
      let uploadZone = this.$refs.uploadZone
      if (uploadZone) {
        const uploader = uploadZone.uploader
        uploader.on('fileSuccess', async (rootFile, file, message) => {

          console.log(message)
          let res = await this.$ajax({
            url: `/uploadFileTrunk/mergeChunks.do`,
            query: {
              fileMd5: file.uniqueIdentifier,
              fileName: file.name
            }
          })
          if(file.fileType.startsWith('image')){
            this.form.unreelList.push({
              fileName: file.name,
              fileUrl: res
            })
          }else{
            this.form.invigilateList.push({
              fileName: file.name,
              fileUrl: res
            })
          }
        })
        uploader.on('fileRemoved', async ( file) => {
          // this.attachmentList.some((e,i)=>{
          //   if(e.name === file.name){
          //     this.attachmentList.splice(i,1)
          //     return true
          //   }
          // })
        })
      }
    }
  }
</script>
